<template>
            <INPUT class="FORM-ELEMENT" 
            :type="computedType" 
            :id="computedID" 
            :value="computedValue"
            :checked="computedValue"
            @reportRequest="submit" 
            :placeholder="computedPlaceHolder"
            />
</template>
<script>
export default {
    name:'INPUT-BOX',
    props:['data'],
    methods:
    {
        submit()
        {
            this.$say(this.computedID||"90",{data:this.computedValue||this.value()})
        },
        value()
        {
            return this.$el.value
                ||this.$el.checked;
        }
    },
    computed:{
        computedType()
        {
            return this.type
                ||this.params.type
                ||this.data.type||'text';
        },
        computedID()
        {
            return this.id
                ||this.params.id
                ||this.data.id
                ||'text';
        },
        computedPlaceHolder()
        {
            return this.placeHolder
                ||this.params.Holder
                ||this.data.Holder
                ||'';
        },
        computedValue()
        {
            return this.params.value
                ||this.data.value
                ||'';
        }
    }
}
</script>


